.container {
  background-color: #F9F9F9;
  padding: 40px 0;
}

.inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
}

.section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 7%;
  color: #939393;
  line-height: 1.4;
}

.label {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.link {
  text-decoration: none;
  color: #939393;
  font-family: serif;
  font-size: 14px;
  font-weight: 300;
}

@media (max-width: 1023px) {
  .inner {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .section {
    margin: 0 0 25px;
    text-align: center;
  }

  .label {
    text-decoration: underline;
    font-size: 23px;
  }

  .link {
    padding: 10px 0;
    background-color: #F9F9F9;
    color: #939393;
    font-size: 16px;
  }
}



